import prisma from "@/lib/prisma";
import authSeller from "@/middlewares/authSeller";
import { getAuth } from "@clerk/nextjs/server";
import { NextResponse } from "next/server";

export async function POST(request){
    try {
        const { userId } = getAuth(request)
        const storeId = await authSeller(userId)

        if(!storeId){
            return NextResponse.json({ error: 'not authorized' }, { status: 401 })
        }

        const { orderId, status, reason } = await request.json()
        
        const updateData = { status }
        
        // Handle Rejection
        if (status === 'REJECTED' && reason) {
            updateData.rejectionReason = reason
            updateData.status = 'REJECTED' 
            // NOTE: In a production app, trigger your Refund Payment Logic here (e.g., Stripe Refund)
        }

        const updatedOrder = await prisma.order.update({
            where: { id: orderId, storeId },
            data: updateData
        })

        return NextResponse.json({ message: "Order Status updated", order: updatedOrder })
    } catch (error) {
        console.error(error);
        return NextResponse.json({ error: error.code || error.message }, { status: 400 })
    }
}

export async function GET(request){
    try {
        const { userId } = getAuth(request)
        const storeId = await authSeller(userId)

        if(!storeId){
            return NextResponse.json({ error: 'not authorized' }, { status: 401 })
        }

        const orders = await prisma.order.findMany({
            where: {storeId},
            include: { user: true, address: true, orderItems: { include: { product: true } } },
            orderBy: { createdAt: 'desc' }
        })

        return NextResponse.json({orders})
    } catch (error) {
        console.error(error);
        return NextResponse.json({ error: error.code || error.message }, { status: 400 })
    }
}